import styled from "styled-components";
import { useProgress } from "../../../contexts/ProgressContext";
import { Block } from "../Block";
import { Modal } from "./Modal";
import { Button } from "../Button";
import { useEffect, useState } from "react";

const Content = styled(Block)`
    margin-top: var(--spacing_x5);
`;

const ButtonsWrapper = styled.div`
    display: flex;
    justify-content: space-between;
    margin-top: var(--spacing_x4);
    align-items: center;

    & button {
        width: calc((100% - var(--spacing_x3) + (var(--spacing_x1) / 2))/2);
    }
`;

const ButtonStyled = styled(Button)`
    margin-top: var(--spacing_x4);
`;

export const InfoModal = ({onClose, isShown}) => {
    const [part, setPart] = useState(0);
    const { setUserInfo, updateUser, passedWeeks } = useProgress();

    const handleGoLobby = () => {
        updateUser({seenRules: true});
        setUserInfo({seenRules: true});
        onClose();
    }

    useEffect(() => {
        if (!isShown) setPart(0);
    }, [isShown])

    const getContent = () => {
        switch (part) {
            case 0:
                return (
                    <Content onClose={onClose}>
                        <p>
                              !{'\n'}
                                   .{' '}
                                       !
                        </p>
                        <ButtonStyled onClick={() => setPart(prev => prev + 1)}></ButtonStyled>
                    </Content>
                )
            case 1: 
                return (
                    <Content onClose={onClose}>
                        <p>
                            <b> </b>   10 ,{' '}
                                .{'\n'}
                             40      <b> </b>.{'\n\n'}
                               50    <b> </b>   50 000 .{' '}
                        </p>
                        <ButtonsWrapper>
                            <Button color="white" onClick={() => setPart(prev => prev - 1)}></Button>
                            <Button onClick={() => !passedWeeks.includes(1) ? setPart(prev => prev + 1) : handleGoLobby()}></Button>
                        </ButtonsWrapper>
                    </Content>
                );
            case 2: 
                return (
                    <Content onClose={onClose}>
                        <p>
                            <b>    .</b>{'\n\n'}
                               ,    !
                        </p>
                        <ButtonsWrapper>
                            <Button color="white" onClick={() => setPart(prev => prev - 1)}></Button>
                            <Button onClick={handleGoLobby}></Button>
                        </ButtonsWrapper>
                    </Content>
                ) 
            default: break;
        }
    }

    return (
        <Modal isShown={isShown}>
            {getContent()}
        </Modal>
    )
}